@import url(https://fonts.googleapis.com/css?family=Quicksand:500,700);@import url(https://cdnjs.cloudflare.com/ajax/libs/material-design-icons/3.0.1/iconfont/material-icons.min.css);
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  background-color: #ffd600;
}
html *, body * {
    font-family: 'Quicksand', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
a {
  text-decoration: none;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  top: 80px;
  padding: 15px;
  width: 100%;
  max-width: 600px;
  margin: auto;
}
#app .input--create {
    width: 100%;
    height: 60px;
    font-size: 20px;
    padding: 0 20px;
    border: 1px solid #FFD600;
    border-radius: 3px;
    outline: none;
    margin-bottom: 10px;
}
.container {
  position: relative;
  /*   width: 600px; */
  padding: 10px;
  padding-bottom: 5px;
  border-radius: 3px;
  margin-bottom: 100px;
}
.todoList {
  margin: 0;
  padding: 0;
}
.todoList li {
    position: relative;
    background-color: #ffe03d;
    list-style: none;
    font-size: 0.98rem;
    /*     height: 60px; */
    line-height: 20px;
    margin-bottom: 8px;
    padding: 20px 20px;
    padding-right: 50px;
    border-radius: 3px;
    color: #3d3300;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.todoList li:hover .modify {
      display: block;
}
.filter {
  position: relative;
}
.filter a {
    font-size: 0.8125rem;
    background: #b39600;
    color: white;
    padding: 8px 10px;
    display: inline-block;
    border-radius: 2px;
    margin-bottom: 10px;
}
.filter .right {
    float: right;
}
@media only screen and (max-width: 480px) {
.filter .right {
        float: none;
}
}
.filter .right span {
      color: #4d4000;
      padding-left: 10px;
}
.modify {
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -10px;
  opacity: 0.5;
  line-height: 20px;
  display: none;
  font-weight: bold;
}
.edit--input {
  position: absolute;
  left: 0;
  height: 100%;
  top: 0;
  width: 100%;
  padding: 0 20px;
  font-size: 16px;
  border: none;
  background: #ffe142;
  border: 1px solid #8e9526;
  color: #725600;
  border-radius: 3px;
  outline: none;
}
.filter--input {
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  font-size: 18px;
  padding: 0 20px;
  border: 1px solid #998000;
  border-radius: 3px;
  outline: none;
}
.completed--icon {
  line-height: 20px;
  vertical-align: top;
  font-size: 20px;
  padding-right: 5px;
}
.selected {
  background: #665600 !important;
}
.completed {
  background-color: #C0CA33 !important;
  color: #4c5014 !important;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
